  padding-top: 200px;
}
@import "variables.scss";
@import "mixin.scss";
@import url("https://fonts.googleapis.com/css?family=Source+Sans+Pro:400,700&display=swap");
* {
  margin: 0;
  padding: 0;
  font-family: "Source Sans Pro";
  font-size: 18px;
  color: #ffffff;
  letter-spacing: -0.03px;
  text-align: left;
  color: #000;
  line-height: 24px;
}
body {
  background-color: #fff;
}
input,
textarea {
  border: 1px solid #000000;
  background-color: transparent;
  line-height: 40px;
  height: 40px;
  padding-left: 10px;
  width: 100%;
}
label {
  font-size: 24px;
  color: #000000;
  letter-spacing: -0.04px;
}
input.butt {
  height: 70px;
  line-height: 70px;
  width: 100%;
  background: #1f3257;
  box-shadow: inset 0 -7px 3px 0 rgba(0, 0, 0, 0.5);
}
textarea {
  line-height: 24px;
  padding: 20px;
  font-weight: 300;
}
input:focus,
input:active {
  outline: none;
}
h3 {
  font-size: 30px;
  color: #000000;
  letter-spacing: 0.56px;
  font-weight: 700;
  line-height: 50px;
}
a {
  font-size: 24px;
  &:hover {
    color: inherit;
    text-decoration: none;
  }
}
a.logo {
  display: block;
  margin: 0 20px;
  img {
    width: 120px;
  }
}
img {
  width: 100%;
}
.butt {
  background-color: #4c493f;
  border: 0;
  outline: 0;
  padding: 0;
  margin-left: auto;
  margin-right: auto;
  display: block;
  width: 210px;
  line-height: 40px;
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.2);
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  font-size: 20px;
  cursor: pointer;
  &:hover {
    box-shadow: none;
    color: white;
  }
  &:active {
    font-size: 18px;
  }
}
#burger {
  position: absolute;
  top: 58px;
  left: 15px;
  z-index: 10;
  cursor: pointer;
  height: 30px;
  width: 30px;
  text-align: center;
  float: right;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  display: none;
  &:after,
  &:before {
    content: "";
    height: 2px;
    width: 30px;
    border-radius: 0px;
    display: inline-block;
    position: absolute;
    left: 0;
    background-color: #000000;
    transition: all 0.2s ease-out;
  }
  &:after {
    bottom: 6px;
  }
  &:before {
    top: 6px;
  }
  .iconBar {
    width: 30px;
    height: 2px;
    display: inline-block;
    margin: 14px 0;
    background-color: #000000;
    transition: all 0.2s ease-out;
    border-radius: 3px;
  }
}
#burger.burgerOpen {
  .iconBar {
    width: 0;
  }
  &:after {
    transform: rotate(-45deg);
    bottom: 14px;
  }
  &:before {
    transform: rotate(45deg);
    top: 14px;
  }
}
.oneProductBlock {
  position: relative;
  &:hover .productOptions {
    display: flex;
  }
  a {
    font-size: 20px;
    color: #000000;
    letter-spacing: 0;
    line-height: 30px;
    text-transform: uppercase;
    text-align: center;
    display: block;
  }
  .productDescr {
    position: relative;
    z-index: 2;
    @include fonTechniquePages;
  }
  .price {
    display: flex;
    width: 100%;
    justify-content: center;
    span {
      font-size: 20px;
      line-height: 30px;
      color: #000000;
      letter-spacing: 0;
      display: block;
      width: auto;
      margin: 0 20px;
      &:nth-child(2) {
        text-decoration: line-through brown solid;
      }
    }
  }
  .reduce {
    position: absolute;
  }
  .productOptions {
    position: absolute;
    display: none;
    width: 100%;
    padding: 0 10px;
    height: 60px;
    bottom: 60px;
    align-items: center;
    justify-content: space-between;
    @include fonTechniquePages;
    & > div {
      height: 40px;
      margin-left: 10px;

      &:first-child {
        margin-left: 0;
      }
    }
    & > div:first-child {
      cursor: pointer;
      width: 30px;
      background: no-repeat center;
      background-size: 20px;
      background-image: url(../img/icons/wish.svg);
    }
    & > div:last-child {
      width: 30px;
      background: no-repeat center;
      background-size: 20px;
      background-image: url(../img/icons/cart.svg);
      background-size: 25px;
      cursor: pointer;
    }
    .selectBox {
      min-width: 110px;
      width: calc(50% - 20px);
    }
  }
}

.selectBox {
  position: relative;
  span {
    font-size: 14px;
    color: #4c493f;
    letter-spacing: 0;
    text-align: left;
    line-height: 40px;
  }
  .optionSelectedBox {
    border: 1px solid #000000;
    padding-left: 10px;
    height: 40px;
    cursor: pointer;
    background-image: url(../img/icons/arrowDown.svg);
    background-repeat: no-repeat;
    background-position: 95%;
    white-space: nowrap;
    overflow: hidden;
    line-height: 40px;
  }
  .optionSelectedBox.valueSelected {
    text-transform: uppercase;
  }
  .options {
    @include fonTechniquePages;
    height: 0;
    overflow: hidden;
    left: 0;
    width: 100%;
    position: absolute;
    margin-top: -1px;
    transition: height 0.3s ease;
    min-width: 190px;
    z-index: 5;
  }
  .option {
    position: absolute;
    width: 100%;
    left: 0;
    display: flex;
    justify-content: space-between;
    padding: 0 10px;
    span {
      display: block;
      border: 0;
    }

    span:first-child {
      text-transform: uppercase;
    }
  }
  .containerSize.limitedStock .option {
    span {
      color: red;
    }
  }
  .containerSize.soldOut {
    cursor: default;
    pointer-events: none;
    input {
      cursor: default;
    }
    &:hover {
      background-color: transparent;
    }
    .option {
      cursor: default;
    }
    .option span {
      color: #8c8c8c;
    }
  }
  .containerSize {
    display: block;
    position: relative;
    height: 40px;
    border-bottom: 1px solid #000;
    input:checked ~ .option {
      background-color: rgba(0, 0, 0, 0.3);
    }
    input {
      position: absolute;
      height: 40px;
      cursor: pointer;
      opacity: 0;
      margin-bottom: 0;
      z-index: 1;
    }
    &:hover input ~ .option {
      background-color: rgba(0, 0, 0, 0.3);
    }
    &:last-child {
      border: 0;
    }
  }
  .sizeGide {
    font-size: 14px;
    line-height: 40px;
    border-bottom: 1px solid #000000;
    padding-left: 40px;
    background-image: url(../img/icons/sizdeGid.svg);
    background-repeat: no-repeat;
    background-position: 7px center;
    text-decoration: underline;
    cursor: pointer;
  }
}
.selectBox.active {
  .options {
    height: auto;
    border: 1px solid black;
  }
  .optionSelectedBox {
    background-image: url(../img/icons/arrowUp.svg);
  }
}
.selectBox.selectBoxQty {
  .options {
    left: auto;
    right: 0;
  }
}

@media only screen and (max-width: 1200px) {
  .oneProductBlock {
    .productOptions {
      position: static;
    }
  }
}
@media only screen and (max-width: 768px) {
  #burger {
    display: block;
  }
  .butt {
    font-size: 16px;
  }
  h3 {
    font-size: 25px;
    line-height: 35px;
  }
}

//  HEADER

header {
  height: $heightHeader;
  width: 100vw;
  top: 0;
  left: 0;
  right: 0;
  z-index: 5;
  position: fixed;
}
.menuLeft {
  @include fonHome;
  display: flex;
  height: 60px;
  width: 50vw;
  align-items: center;
  margin-bottom: 0;
  span,
  a {
    font-size: 16px;
    color: #000000;
    text-transform: uppercase;
    line-height: 25px;
    line-height: 60px;
  }
  li {
    text-align: center;
    line-height: 60px;
    position: relative;
    padding-right: 15px;
    &:last-child {
      min-width: 120px;
    }
    &:hover .subMenu {
      opacity: 1;
      visibility: visible;
    }
    &:first-child,
    &:nth-child(2) {
      text-align: left;
      cursor: pointer;
      &:hover span {
        background-image: url(../img/icons/iconOnHover.svg);
        background-repeat: no-repeat;
        background-size: 20px;
        background-position: 5px center;
      }
    }
    &:nth-child(3),
    &:nth-child(4) {
      span:hover {
        background-image: url(../img/icons/iconOnHover.svg);
        background-repeat: no-repeat;
        background-size: 20px;
        background-position: 5px center;
      }
    }
    &:nth-child(5) {
      span {
        display: block;
      }
      a {
        display: flex;
      }
    }
  }
  span {
    padding-left: 30px;
  }
}
.subMenu {
  position: absolute;
  left: 0;
  visibility: hidden;
  z-index: 2;
  width: auto;
  @include fonHome;
  padding-right: 2vw;
  padding-top: 20px;
  padding-bottom: 20px;
  box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.5);
  opacity: 0;
  transition: all 0.3s ease;
  li {
    width: 100%;
    height: 35px;
    text-align: left;
    line-height: 35px;
  }
  a {
    padding-left: 30px;
    white-space: nowrap;
    line-height: 35px;
    background-repeat: no-repeat;
    background-size: 20px;
    background-position: 5px center;
    &:hover {
      font-weight: bold;
      background-image: url(../img/icons/iconOnHover.svg);
    }
  }
}
.menuCabinet {
  display: flex;
  justify-content: space-between;
  padding: 0 15px;
  height: $heightHeader;
  width: 25vw;
  min-width: 400px;
  @include fonHome;
  .buttMenu {
    height: $heightHeader;
    width: 40px;
    background-repeat: no-repeat;
    background-position: center;
    a {
      display: block;
      text-align: center;
      line-height: $heightHeader;
      width: 100%;
      height: 100%;
      color: white;
      cursor: pointer;
    }
  }
  .buttSearch {
    background-image: url(../img/icons/search.svg);
    background-size: 20px;
    cursor: pointer;
    position: relative;
  }
  .searchOpen.searchOpenActive {
    min-width: 300px;
    width: 25vw;
    z-index: 1;
    height: auto;
    .searchResults {
      height: auto;
    }
  }
  .searchOpen {
    position: absolute;
    right: 55px;
    @include fonHome;
    width: 25vw;
    min-width: 0;
    width: 0;
    height: 60px;
    transition: all 0.3s ease;
    z-index: -1;
    input {
      width: 100%;
      margin-top: 10px;
      border: 0;
      border-bottom: 1px solid black;
      font-size: 18px;
      color: #000;
      margin-bottom: 10px;
    }
    .searchResults {
      position: relative;
      @include fonHome;
      width: 100%;
      display: block;
      height: 0;
      overflow: hidden;
      box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.5);
      padding-top: 10px;
    }
    li,
    a {
      line-height: 30px;
      color: #000;
      text-align: left;
      font-size: 18px;
      white-space: nowrap;
    }
    a:hover {
      text-decoration: underline;
    }
    li {
      padding: 0 20px;
    }
  }
  .buttWish {
    background-image: url(../img/icons/wish.svg);
    background-size: 20px;
  }
  .buttCart {
    background-image: url(../img/icons/cart.svg);
    background-size: 18px;
    a {
      position: relative;
    }
    .nrArt {
      position: absolute;
      width: 27px;
      height: 27px;
      background-image: url(../img/icons/star.svg);
      background-size: 100%;
      line-height: 27px;
      text-align: center;
      font-size: 14px;
      color: white;
      top: 5px;
      right: -7px;
    }
  }
  .buttAvatar {
    background-image: url(../img/icons/avatar.svg);
    background-size: 18px;
    cursor: pointer;
  }
  .buttSettings {
    // background-image: url(../img/icons/cogs.svg);
    width: auto;
    a {
      font-size: 18px;
      color: #000;
      text-transform: uppercase;
      line-height: 60px;
      display: flex;
      align-items: center;
      position: relative;
    }
    .flag {
      display: block;
      background-repeat: no-repeat;
      background-image: url(../img/icons/moldova.svg);
      background-size: 100% 100%;
      width: 50px;
      height: 40px;
      margin-left: 3px;
    }
  }
  .menuOpen {
    position: fixed;
    @include fonHome;
    top: $heightHeader;
    right: 0;
    box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.5);
    z-index: 1;
    height: 0;
    transition: height 0.3s ease;
    width: 25vw;
    min-width: 400px;
    overflow: hidden;
  }
  .menuOpen.menuOpenDisplay {
    height: auto;
  }
  .menuOpenResults {
    padding: 0 20px;
  }
}
.fullWidthHeader .headerBlock {
  @include fonHome;
}
.headerBlock {
  display: flex;
  justify-content: space-between;
}
