img.mainImg {
  width: 100%;
  cursor: url("../img/icons/zoomCursor.svg"), auto;
  cursor: image-set(
      url("../img/icons/zoomCursor.svg") 1x,
      url("../img/icons/zoomCursor.svg") 2x
    ),
    auto;
  cursor: -webkit-image-set(
      url("../img/icons/zoomCursor.svg") 1x,
      url("../img/icons/zoomCursor.svg") 2x
    ),
    auto;
}
.julia-zoom {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  opacity: 1;
  display: none;
  cursor: url("../img/icons/miniZoomCursor.svg"), auto;
  cursor: image-set(
      url("../img/icons/miniZoomCursor.svg") 1x,
      url("../img/icons/miniZoomCursor.svg") 2x
    ),
    auto;
  cursor: -webkit-image-set(
      url("../img/icons/miniZoomCursor.svg") 1x,
      url("../img/icons/miniZoomCursor.svg") 2x
    ),
    auto;
  /* cursor: url('../img/icons/miniZoomCursor.svg'), auto; */
  overflow: hidden;
}
.julia-zoom img {
  position: absolute;
  top: 0;
  left: 0;
}
.closeZoomImg {
  position: fixed;
  top: 25px;
  right: 25px;
  width: 25px;
  height: 25px;
  z-index: 10;
  cursor: pointer !important;
  background-image: url(../img/icons/close.svg);
  background-repeat: no-repeat;
  background-position: center center;
  -webkit-background-size: 25px 25px;
  background-size: 25px 25px;
}
#controlZoomImg {
  display: block;
  position: fixed;
  z-index: 10;
  width: 80px;
  height: 100vh;
  top: 0;
  left: 15px;
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
#controlZoomImg img {
  width: 100%;
  margin-top: 15px;
  display: block;
  position: static;
  opacity: 0.4;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}
#controlZoomImg img.mainImg.activeMainImg {
  opacity: 1;
}

//  cart page

.cartContent {
  @include fonTechniquePages;
  margin-top: 50px;
  padding-bottom: 130px;
  .bagFromFirefox {
    width: calc(100% - 400px);
    height: 100%;
  }
  .container {
    max-width: 1200px;
  }
  h3 {
    padding: 30px 0;
    text-align: center;
    font-size: 30px;
  }
  .deliveryCart {
    .deliveryItem {
      background: black;
      padding: 15px 5px;
      margin: 10px 0;
    }
    .deliveryTitle {
      font-family: "Source Sans Pro";
      font-size: 30px;
      text-align: center;
      color: white;
    }
    .deliveryItem p {
      color: white;
      font-size: 18px;
      text-align: center;
      font-family: "Source Sans Pro";
    }
  }
  .cartBloc {
    padding-bottom: 30px;
    margin-top: 30px;
    height: 100%;
    padding-left: 15px;
    padding-right: 15px;
    justify-content: space-between;
  }
  .saveForLaterBox {
    margin: 40px 0;
    .title {
      font-family: Helvetica;
      font-size: 24px;
      color: #4c493f;
      margin-bottom: 10px;
    }
  }
  .blocReview {
    margin-top: 50px;
    .title {
      font-size: 24px;
      color: #000000;
      letter-spacing: 0;
      margin-bottom: 10px;
    }
  }
  .paymentMethods {
    input[type="submit"] {
      width: 225px;
      margin-top: 30px;
    }
  }
}
.itemCart {
  display: flex;
  position: relative;
  border-top: 1px solid #8c8c8c;
  padding: 20px 0;
  .descrBloc {
    display: flex;
    width: 70%;
    a {
      display: block;
      margin-right: 20px;
    }
    img {
      width: 80px;
      box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.2);
    }
    .name {
      font-size: 20px;
      color: #000000;
      letter-spacing: 0;
      font-weight: bold;
    }
    .size {
      font-size: 18px;
      color: #4c493f;
      & span:nth-child(2) {
        margin-left: 10px;
      }
    }
  }
  .functionalOptions > div {
    text-decoration: none;
  }
  .functionalOptions {
    display: flex;
    margin-top: 5px;
    position: relative;
    div {
      font-size: 18px;
      color: #4c493f;
      cursor: pointer;
      &.saveForLater {
        margin-left: 20px;
      }
    }
    .optionsOpen {
      display: none;
      width: 170px;
      @include fonTechniquePages;
    }
    .selectBox {
      width: 170px;
      &:nth-child(2) {
        margin-top: 10px;
      }
    }
  }
  .saveupdateBloc {
    display: flex;
    width: 100%;
    margin-top: 10px;
    div {
      text-align: center;
      display: block;
      text-transform: uppercase;
      line-height: 30px;
      color: white;
      &:last-child {
        background-color: #000;
        margin-left: 10px;
      }
    }
  }
  .priceBloc {
    div {
      text-transform: uppercase;
      font-size: 20px;
      color: #000000;
      letter-spacing: 0;
    }
    div:first-child {
      font-weight: 700;
    }
    div:last-child {
      font-size: 18px;
      background-image: url(../img/icons/reduceLine.svg);
      background-repeat: no-repeat;
      background-position: center;
      background-size: 100% 40px;
      margin-left: -5px;
      padding-left: 5px;
    }
  }
  .delete {
    width: 30px;
    height: 30px;
    background-image: url(../img/icons/close.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 20px;
    cursor: pointer;
    margin-left: auto;
    margin-right: 0;
    &:active {
      background-size: 15px;
    }
  }
}
.cartSet {
  .functionalOptions {
    .optionsOpen > .selectBox {
      margin-top: 10px;
    }
    .optionsOpen {
      width: auto;
      .itemCart {
        margin-top: 20px;
      }
    }
  }
}
.infoOrder {
  width: 370px;
  margin-left: 15px;
  position: sticky;
  top: 80px;
  z-index: 3;
  height: auto;
  .orderSummary {
    background-image: url(../img/icons/concrette.jpg);
    background-size: 100%;
    padding: 15px;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
    position: relative;
    z-index: 9999;
    &:first-child {
      margin-bottom: 30px;
    }
    &:nth-child(2) {
      padding-bottom: 30px;
    }
  }
  .title {
    font-size: 24px;
    color: #000000;
    letter-spacing: 0;
  }
  .bloc {
    display: flex;
    justify-content: space-between;
    margin: 10px 0;
    div {
      font-size: 18px;
      line-height: 25px;
    }
    select {
      width: 230px;
      font-size: 18px;
      line-height: 25px;
      background-color: transparent;
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
      border-radius: 0;
      border: 2px solid black;
      padding-left: 10px;
      cursor: pointer;
      background-image: url(../img/icons/arrowDown.svg);
      background-repeat: no-repeat;
      background-position: 95% center;
    }
  }
  input {
    border-radius: 0;
    border: 2px solid black;
    padding-left: 10px;
    line-height: 30px;
    height: 30px;
    margin-bottom: 0;
  }
  input,
  select {
    margin-bottom: 0;
  }
  input[type="submit"] {
    margin-left: auto;
    margin-right: 0;
    display: block;
  }
  .promoCode {
    input:first-child {
      width: 60%;
    }
    input:last-child {
      width: 130px;
      text-align: center;
      color: white;
      background-color: #000;
      padding: 0;
      margin: 0;
      text-transform: uppercase;
      border: 0;
    }
  }
}
.boxBloc {
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
  padding: 30px;
  margin: 30px 0;
  .title {
    font-family: Helvetica;
    font-size: 24px;
    color: #000000;
    margin-bottom: 20px;
  }
  .oneProductBlock {
    width: 164px;
    margin: 0 10px;
    .productDescr {
      a {
        font-size: 16px;
        white-space: nowrap;
        overflow: hidden;
      }
      .price span {
        font-size: 14px;
        margin: 0 5px;
      }
    }
  }
  .slick-arrow {
    position: absolute;
    top: -40px;
    font-size: 0;
    border: 0;
    padding: 0;
    background: none;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100%;
    height: 20px;
    width: 30px;
    right: 0;
    z-index: 2;
    opacity: 0.5;
    &:active {
      background-size: 35%;
    }
    &:focus {
      outline: 0;
    }
  }
  .slick-next {
    background-image: url(../img/icons/next.svg);
  }
  .slick-prev {
    background-image: url(../img/icons/prev.svg);
    right: 35px;
  }
}
input[type="submit"] {
  color: white;
  text-align: center;
  text-transform: uppercase;
  background-color: #000;
  margin-top: 20px;
  line-height: 35px;
  height: 35px;
  border: 0;
  padding: 0;
  transition: all 0.2s ease;
}
.shippingInformation {
  .title {
    font-size: 28px;
    color: #000000;
    letter-spacing: 0;
  }
  .shippingBloc {
    margin-top: 30px;
    .title {
      font-size: 18px;
      color: #000000;
      letter-spacing: 0;
      border-bottom: 1px solid #979797;
      margin-bottom: 20px;
      line-height: 30px;
    }
  }
  label {
    font-size: 14px;
    color: #4c493f;
    letter-spacing: 0;
  }
  b {
    color: red;
  }
  input {
    margin-bottom: 20px;
  }
  .cancelStep {
    background: #8c8c8c;
    line-height: 35px;
    height: 35px;
    color: white;
    text-transform: uppercase;
    text-align: center;
    margin-top: 20px;
    cursor: pointer;
  }
}
.tabBox {
  margin-bottom: 30px;
  .tabHeader {
    position: relative;
    display: flex;
  }
  .tabOption {
    position: relative;
    display: block;
    width: 25%;
    margin-left: 10px;
    cursor: pointer;
    &:first-child {
      margin-left: 0;
    }
    input {
      opacity: 0;
      position: absolute;
      height: 0;
      width: 0;
    }
    input:checked ~ .content {
      background: rgba(216, 216, 216, 0.91);
      border: 1px solid #000000;
      font-weight: bold;
    }
  }

  .content {
    text-align: center;
    font-size: 18px;
    color: #000000;
    letter-spacing: 0;
    display: block;
    padding: 20px 10px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: rgba(216, 216, 216, 0.51);
    border: 1px solid #979797;
    .imgBloc {
      display: block;
      text-align: center;
      img {
        width: 30px;
      }
    }
    img {
      width: 100px;
      margin-left: auto;
      margin-right: auto;
      margin-top: 10px;
    }
  }
  .tabContent {
    padding-top: 30px;
  }
  .tabOpen {
    display: none;
  }
  .tabOpen.active {
    display: block;
  }
}
.cardCredit {
  display: flex;
  .cvCard {
    width: 70px;
    position: absolute;
    bottom: 50px;
    right: 5px;
    label {
      text-align: right;
    }
  }
  & > .row {
    position: relative;
    margin: 0;
    width: 320px;
    opacity: 0.89;
    background: #d8d8d8;
    border: 1px solid #979797;
    border-radius: 10px;
    padding: 20px;
    margin-right: 10px;
  }
  label {
    font-size: 14px;
    color: #8c8c8c;
    letter-spacing: 0;
    text-transform: uppercase;
  }
  input {
    margin-bottom: 20px;
    border: 0;
    border-bottom: 1px solid #979797;
    padding: 0;
    line-height: 20px;
    height: 20px;
  }
  ::placeholder {
    text-transform: uppercase;
  }
}
.checkContainer {
  font-size: 16px;
  color: #000000;
  letter-spacing: 0;
  position: relative;
  display: block;
  padding-left: 30px;
  cursor: pointer;
  input {
    width: 0;
    height: 0;
    position: absolute;
    opacity: 0;
  }
  span {
    display: block;
    width: 15px;
    height: 15px;
    border: 1px solid black;
    position: absolute;
    left: 0;
    top: 5px;
  }
  input:checked ~ span {
    background-image: url(../img/icons/checked.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: 10px;
  }
  &:hover span {
    background-color: #d8d8d8;
  }
}
@media only screen and (max-width: 768px) {
  .infoOrder {
    width: 100%;
    height: 100vh;
    margin-left: 0;
    position: fixed;
    bottom: 0;
    top: auto;
    z-index: 10;
    left: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    transition: all 0.3s ease;
    background-image: url(../img/icons/concrette.jpg);
    background-size: 100%;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
    .orderSummary {
      max-width: 400px;
      margin-left: auto;
      margin-right: auto;
      width: 100%;
      padding-top: 0;
      background: transparent;
      box-shadow: none;
      transition: all 0.5s ease;
      &:first-child {
        transition: all 0.5s ease;
        margin-bottom: 0;
        padding-top: 15px;
        .title {
          transition: all 0.5s ease;
        }
      }
    }
    #orderButton {
      position: absolute;
      z-index: 2;
      height: 30px;
      width: 40px;
      background-image: url(../img/icons/arrowUp.svg);
      background-position: 100% center;
      background-repeat: no-repeat;
      background-size: 20px;
      margin-bottom: 20px;
      top: 10px;
      margin-left: auto;
      margin-right: auto;
      left: 0;
      right: 0;
      transform: rotate(180deg);
    }
    .orderTotal {
      transition: all 0.2s ease;
    }
  }
  .infoOrder.infoOrderOpen {
    transition: all 0.3s ease;
    height: 80px;
    .orderSummary {
      max-width: 100%;
      &:first-child {
        padding-bottom: 0;
        padding-top: 0;
        transition: all 0.3s ease;
        .title {
          transition: all 0.3s ease;
          display: block;
          margin-bottom: 0;
          top: 20px;
          font-size: 20px;
          position: absolute;
        }
      }
      div {
        display: none;
      }
      input[type="submit"] {
        width: 130px;
        margin-right: 0;
        margin-left: auto;
        display: block;
        margin-top: 30px;
      }
      .orderTotal {
        position: fixed;
        bottom: 0px;
        left: 15px;
        display: flex;
        transition: all 0.3s ease;
        div {
          display: block;
          font-size: 18px;
          line-height: 35px;
          &:nth-child(2) {
            margin-left: 20px;
          }
        }
      }
    }
    #orderButton {
      background-image: url(../img/icons/arrowDown.svg);
    }
  }
  .cartContent {
    margin-top: 100px;
    .deliveryCart {
      display: none;
    }
    .bagFromFirefox {
      width: 100%;
    }
    .cartBloc {
      margin-top: 0;
    }
  }
  .productsInCart {
    overflow: hidden;
  }
  .itemCart {
    position: relative;
    .priceBloc {
      position: absolute;
      top: 60px;
      right: 20px;
      div {
        font-size: 18px;
        line-height: 20px;
      }
    }
    .descrBloc {
      width: 100%;

      & > div {
        margin-top: 40px;
      }
      .name {
        font-size: 18px;
        line-height: 20px;
        max-width: 70%;
      }
    }
    .optionsOpen {
      position: relative;
      width: 100vw;

      .itemCart {
        position: relative;
        margin-left: -100px;
        margin-right: 100px;
        .priceBloc {
          top: 20px;
          right: 0;
        }
        .descrBloc > div {
          margin-top: 0px;
        }
      }
      .saveupdateBloc {
        margin-left: -100px;
      }
    }
  }
  .boxBloc {
    padding-left: 5px;
    padding-bottom: 0;
    padding-right: 5px;
  }
}

/* footer */
footer {
  background-color: $footerBcg;
  background-image: url(../img/icons/footer.jpg);
  background-size: 100% 100%;
  padding-top: 50px;
  ul {
    margin-bottom: 0;
  }
  a,
  .footerTitle {
    font-family: $footerFont;
    font-size: 16px;
    color: #ffffff;
    letter-spacing: 0;
    line-height: 30px;
  }
  a:hover {
    color: white;
    opacity: 0.7;
    text-decoration: none;
  }
  .footerTitle {
    text-transform: uppercase;
    padding: 5px 0;
  }
  .retFooter {
    display: flex;
    justify-content: center;
    margin: 30px 0;
    a {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 50px;
      height: 50px;
      margin: 0 20px;
      &:hover {
        background: #343b49;
        box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.97);
        border-radius: 24px;
        border: 0;
      }
    }
    a.f img {
      width: 23%;
    }
    a.i img {
      width: 40%;
    }
    img {
      width: 40%;
    }
  }
  p {
    font-family: $footerFont;
    font-size: 14px;
    color: #c9c9c9;
    letter-spacing: 0;
    text-align: center;
    line-height: 20px;
    margin-bottom: 20px;
  }
  .prefooter {
    background-image: url(../img/icons/prefooter.jpg);
    font-family: Montserrat;
    font-size: 16px;
    color: #c9c9c9;
    text-align: center;
    line-height: 50px;
  }
}
@media only screen and (max-width: 768px) {
  footer {
    background-size: cover;
    .footerBloc ul {
      height: 0;
      transition: all 0.3s ease;
      overflow: hidden;
    }
    .footerBloc {
      border-bottom: 1px solid $bcgHeader;
      a {
        line-height: 40px;
      }
    }
    .footerTitle {
      background-repeat: no-repeat;
      background-position: 98% center;
      line-height: 50px;
    }
    .bcgPlusFooter {
      background-image: url(../img/icons/plusFooter.svg);
    }
    .bcgMinusFooter {
      background-image: url(../img/icons/minusFooter.svg);
    }
    .retFooter {
      a {
        width: 30px;
        height: 30px;
      }
    }
  }
}
#login,
#settings {
  .modal-dialog {
    max-width: 360px;
    margin: auto;
    margin-top: 2rem;
  }
  .modal-content {
    padding: 15px;
    height: auto;
    padding-bottom: 30px;
  }
  .closeModal {
    position: absolute;
    top: 5px;
    right: 5px;
    width: 30px;
    height: 30px;
    cursor: pointer;
    background-image: url(../img/icons/close.svg);
    background-repeat: no-repeat;
    background-size: 15px;
    background-position: center;
  }
  .modalHeader {
    display: flex;
    position: relative;
    margin-top: 30px;
    width: 100%;
    line-height: 35px;
    margin-bottom: 20px;
    .headTab {
      cursor: pointer;
      font-size: 18px;
      color: #000000;
      letter-spacing: -0.19px;
      text-align: left;
      width: 50%;
      text-align: center;
      border: 1px solid black;
    }
    .headTab.active {
      font-weight: bold;
      border: 0;
      cursor: default;
      pointer-events: none;
    }
  }
  label {
    font-size: 14px;
    color: #4c493f;
    letter-spacing: 0;
    margin-bottom: 10px;
  }
  input {
    margin-bottom: 20px;
  }
  input[type="submit"] {
    margin-bottom: 10px;
  }
  a {
    font-size: 14px;
    color: #000000;
    letter-spacing: 0;
    text-decoration: underline;
  }
  .text-center {
    font-size: 14px;
    color: #4c493f;
    letter-spacing: 0;
    margin-bottom: 10px;
    margin: 20px auto;
  }
  .socialMedia {
    display: flex;
    justify-content: space-between;
    li {
      display: block;
      border: 1px solid black;
      width: 85px;
      height: 40px;
    }
    a {
      display: block;
      width: 100%;
      height: 100%;
      background-position: center;
      background-repeat: no-repeat;
    }
    .f {
      background-image: url(../img/icons/f.svg);
    }
    .i {
      background-image: url(../img/icons/i.svg);
    }
    .gp {
      background-image: url(../img/icons/gp.svg);
    }
  }
  .butt {
    color: white;
    margin-top: 30px;
    text-decoration: none;
  }
  .activeBloc {
    display: block !important;
  }
  .checkContainer {
    width: 60%;
  }
  .signinBloc {
    display: none;
  }
  .registerBloc {
    display: none;
    input[type="submit"] {
      width: 300px;
      margin-left: auto;
      margin-right: auto;
      display: block;
    }
  }
}
#settings {
  select {
    border: 1px solid #000;
    padding-left: 10px;
    height: 40px;
    cursor: pointer;
    background-image: url(../img/icons/arrowDown.svg);
    background-repeat: no-repeat;
    background-position: 95%;
    white-space: nowrap;
    overflow: hidden;
    line-height: 40px;
  }
}
body.bodyHidden {
  overflow: hidden;
}
